/* ================================
   TOOLS SECTION
================================ */
.tools-section {
  padding: 2rem;
}

/* Scenario Buttons */
.tools-scenarios {
  margin-bottom: 1rem;
  text-align: center;
}

.tools-scenarios button {
  margin: 0.5rem;
  padding: 0.75rem 1.5rem;
  background-color: #008080;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-weight: bold;
  transition: background 0.3s ease-in-out;
}

.tools-scenarios button:hover {
  background-color: #005959;
}

/* Input Labels */
.tools-inputs label {
  display: block;
  font-weight: bold;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  text-align: left;
  color: #333;
}

.tools-inputs input[type='range'] {
  width: 100%;
  margin: 0.5rem 0;
  cursor: pointer;
}

/* ================================
   RESULTS & CHART
================================ */
.tools-chart-container {
  margin-top: 2rem;
  background: #f9f9f9;
  padding: 1rem;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);

  /* ADDED: Allows horizontal scrolling if chart is wide on mobile */
  overflow-x: auto;
}

.tools-comparison {
  display: flex;
  justify-content: space-between;
  gap: 1.5rem;
  margin-top: 1rem;
  flex-wrap: wrap; /* wrap on smaller screens */
}

.comparison-box {
  flex: 1 1 300px;
  padding: 1rem;
  background: #f9f9f9;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

/* Summary Section */
.tools-summary {
  margin-top: 2rem;
  text-align: left;
}

.tools-summary p {
  margin-bottom: 1rem;
}

/* ================================
   RESPONSIVE
================================ */
@media (max-width: 768px) {
  .tools-section {
    padding: 1.5rem;
  }
}

@media (max-width: 480px) {
  .tools-section {
    padding: 1rem;
  }

  .tools-scenarios button {
    font-size: 0.9rem;
    padding: 0.5rem 1rem;
  }
}