/* ================================
   1) BASE SECTION STYLING
================================ */
.section {
  padding: 2rem;
  margin: 1rem auto;
  max-width: 900px;
  background: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 12px rgba(0, 128, 128, 0.7);
  text-align: left;

  /* 
    Use a media query to ensure hover only applies 
    on devices that support hovering (e.g., desktop). 
  */
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
}

/* Hover effect only for pointer-based devices */
@media (hover: hover) and (pointer: fine) {
  .section:hover {
    transform: translateY(-3px);
    box-shadow: 0 6px 16px rgba(0, 128, 128, 0.9);
  }
}

/* ================================
   2) HEADINGS
================================ */
.section h2 {
  font-size: 1.8rem;
  color: #008080;
  margin-bottom: 1rem;
  text-align: center;
}

.section h3 {
  font-size: 1.6rem;
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
}

.section h4 {
  font-size: 1.4rem;
  color: #333;
  margin-bottom: 1rem;
  text-align: center;
}

/* ================================
   3) TEXT ELEMENTS
================================ */
.section p {
  font-size: 1rem;
  line-height: 1.7; /* Slightly increased for readability */
  color: #333;
  margin-bottom: 1rem;
}

/* Lists */
.section ul {
  padding-left: 1.5rem;
  list-style-type: square;
}

.section ul li {
  font-size: 1rem;
  line-height: 1.6;
  margin-bottom: 0.5rem;
  font-weight: 500;
}

/* ================================
   4) RESPONSIVE BREAKPOINTS
================================ */
@media (max-width: 768px) {
  .section {
    padding: 1.5rem;
  }

  .section h2 {
    font-size: 1.6rem;
  }
}

@media (max-width: 480px) {
  .section {
    padding: 1rem;
  }

  .section h2 {
    font-size: 1.4rem;
  }

  .section p,
  .section ul li {
    font-size: 0.9rem;
  }
}