/* ===============================
   1) GLOBAL RESETS & BODY STYLING
=============================== */

/* Universal box-sizing reset */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Base body styling */
body {
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* Prevent horizontal scroll on mobile */
  font-family: 'Arial', sans-serif;
  color: white;
  scroll-behavior: smooth;
  min-height: 100vh;

  /* Keep your gradient animations if desired */
  display: flex;
  flex-direction: column;
  position: relative;
}

/* Background gradients/animations (unchanged from your version) */
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(135deg, #2e2e2e, #1a1a1a, #3d3d3d, #262626);
  background-size: 400% 400%;
  animation: metallicShift 10s infinite alternate ease-in-out;
  z-index: -2;
}

body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle, rgba(255, 255, 255, 0.05) 1%, transparent 70%);
  background-size: 50px 50px;
  animation: gridGlow 6s infinite alternate ease-in-out;
  z-index: -1;
}

@keyframes metallicShift {
  0% {
    background-position: 0% 50%;
    filter: brightness(1.1) contrast(1);
  }
  50% {
    background-position: 100% 50%;
    filter: brightness(1.3) contrast(1.1);
  }
  100% {
    background-position: 0% 50%;
    filter: brightness(1.1) contrast(1);
  }
}

@keyframes gridGlow {
  0% {
    opacity: 0.03;
    filter: hue-rotate(0deg);
  }
  50% {
    opacity: 0.07;
    filter: hue-rotate(15deg);
  }
  100% {
    opacity: 0.03;
    filter: hue-rotate(0deg);
  }
}

/* ===============================
   2) APP CONTAINER
=============================== */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  flex-grow: 1;
  position: relative;
  z-index: 1; /* ensures content above background layers */
}

/* ===============================
   3) MAIN CONTENT
=============================== */
main {
  width: 100%;
  max-width: 1200px;
  /* If your header is fixed at ~80px high, margin-top:80px 
     ensures content doesn't hide behind the header. */
  margin-top: 80px;
  flex-grow: 1;
  position: relative;
  z-index: 2;
  border-radius: 10px;
  padding: 2rem;
}

/* ===============================
   4) FOOTER
=============================== */
footer {
  text-align: center;
  padding: 1rem;
  background: #008080;
  color: white;
  width: 100%;
  box-shadow: 0 -2px 6px rgba(0, 0, 0, 0.2);
  position: relative;
  z-index: 2;
}